.createForm {
  width: 80vw;
  display: flex;
  justify-content: space-around;
}
.createFormTitle {
  font-family: "Orbitron", sans-serif;
  text-align: center;
}
.addRecipeDiv {
  display: flex;
  flex-direction: column;
}
.recipeDivFix {
  margin-right: -200px;
  margin-left: 300px;
}
.ingredientForm {
  display: none;
}
.bigButton {
  margin: 0;
}
.deleteButton {
  display: flex;
  justify-content: center;
}
.createRecipeButton {
  width: 200px;
  padding: 5px 5px 50px 5px;
  background-color: #de6c6d;
  display: flex;
  color: white;
}
.updateRecipeButton {
  width: 100px;
  padding: 5px 5px 50px 5px;
  background-color: #de6c6d;
  display: flex;
  color: white;
}
.idField {
  margin: -10px 0 20px 0;
  display: flex;
  justify-content: center;
}
.inputLabel {
  margin-bottom: 0;
}
.subtitle {
  font-size: 10px;
  margin: 10px 0 0 -15px;
}
.updateForm {
  margin: 0 -150px 0 500px;
}
/*form page*/

.addDiv {
  display: none;
}
.addBtn {
  height: 60px;
  width: 200px;
  padding: 10px 25px;
  border: none;
}
.updateDiv {
  display: none;
}
.deleteDiv {
  display: none;
}
.DIDiv {
  display: none;
}
.crudDiv {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}
.display {
  display: block;
}
.formButton {
  width: 90px;
  padding: 5px 20px 50px 5px;
  background-color: #de6c6d;
  display: flex;
  color: white;
}
.DIformButn {
  margin-left: 200px;
}
.buttonFormFix {
  margin-top: -60px;
}
